@import url('https://fonts.googleapis.com/css2?family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&family=Flow+Circular&family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap');

div {
  font-family: "DM Sans", sans-serif;
}

.maintext {
  font-size: 140px;
  font-weight: 300;
  line-height: 1.0;
}

.submaintext {
  font-weight: 300;
  font-size: 20px;
}

.skills-container {
  overflow: hidden;
  display: flex;
  align-items: center;
  position: relative;
}

.move {
  height: 100px;
  width: 100px;

  position: absolute;
  left: calc(100% + (var(--position) * 150px)); /* Increased spacing between the boxes */
  animation: slide 15s linear infinite;
  animation-delay: calc( (15s / 11) * (var(--position) - 1));
}

@keyframes slide {
  0% {
    left: 100%;
  }
  100% {
    left: -150px; /* Increased negative left position to ensure the box is fully out of view */
  }
}

.grad{
  mask-image: linear-gradient(
    to right,
    transparent,
    #000000,
    transparent
    )
}

.skills-container:hover .move{
  animation-play-state: paused!important;
}

.test {
  transform: translateY(0px);
  opacity: 0.7;
  animation: updown 5s linear infinite; /* Reduced duration for a smoother effect */
}

@keyframes updown {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(20px); /* Halfway point, max translation */
  }
  100% {
    transform: translateY(0px); /* Back to start */
  }
}

.test:hover{
  font-size: 50px;
  opacity: 1;
}